html, body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

#ruler {
    position: absolute;
    width: 100%;
    height: 512px;
    z-index: -1;
}

#root {
    height: 100%;
}

.vertical-container {
    border: 0.1px solid teal;
    height: 300px;
    display: -ms-flexbox;
    display: flex;
    -webkit-display: flex;
    -ms-flex-align: center;
        align-items: center;
    -webkit-align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -webkit-justify-content: center;
}


