input[type="range"] {
    -webkit-appearance: none;
    width: 10rem;
    height: .25rem;
    outline: none;
    border-radius: .25rem;
}

input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 1.5rem;
    height: 1.5rem;
    background-color: black;
    border: .25rem solid grey;
    border-radius: 1rem;
    cursor: pointer;
}


